*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
}

.main {
    width: 100%;
    height: 100vh;
    background: #fafafa url(./images/svg-gradient.svg) bottom 10px right/contain no-repeat;
    overflow: hidden;    
}

nav {
    width: 80%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    margin: 0 auto;
}

nav .fa-bars {
    display: none;
}

.logo {
    font-size: 2rem;
    font-weight: bold;
    text-decoration: none;
    color: #2c2c2c;
}

ul {
    display: flex;
}

li {
    list-style: none;
    margin-right: 2rem;
}

a {
    text-decoration: none;
    color: #fff;
}

.nav-btn {
    background: #50cdfb;
    padding: 10px 15px;
    border-radius: 5px;
    color: #fff;
}

.nav-btn:hover {
    background: #f55d5d;
    transition: 0.2s ease-out;
}

.main-content {
    padding-top: 5rem;
    max-width: 80%;
    height: 80vh;
    display: flex;
    align-items: center;
    margin: 0 auto;
    position: relative;
    justify-content: space-between;
}

.main-col-right {
    width: 50%;
}

.main-col-left {
    width: 40%;
    display: flex;
    flex-direction: column;
}

.main-img {
    position: absolute;
    top: 100px;
    /* display: none; */
}

.main-content h1, .main-content p{
    margin-bottom: 1.5rem;
}

.main-content p {
    font-size: 1.5rem;
}

.email {
    outline: none;
    width: 280px;
    height: 50px;
    border: none;
    border-left: 6px solid #5250fb;
    border-radius: 4px;
    box-shadow: 1px 1px 6px -1px rgba(0, 0, 0, 0.25);
    padding-left: 3rem;
    margin-bottom: 1.5rem;
}

.icon {
    padding-left: 25px;
    transform: translate(0%, 100%);
    color: #5250fb;
    width: 70px;
    text-align: left;
    position: absolute;
}

.main-btn {
    background: #5250fb;
    padding: 14px 0;
    border-radius: 4px;
    border: none;
    width: 180px;
    text-align: center;
    color: #fff;
}

.main-btn:hover {
    background: #50cdfb;
    transition: 0.2s ease-out;
}

.new-section {
    color: #fff;
    height: 100vh;
    background-color: #000;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width: 1300px) {
    .main {
        background: #fafafa url(./images/svg-gradient.svg) bottom 10px right -150px/contain no-repeat;
    }
}

@media screen and (max-width: 1000px) {
    .main {
        background: #fafafa url(./images/svg-gradient.svg) top right -400px/contain no-repeat;
    }

    .main-col-left p {
        font-size: 1.2rem;
    }

    nav ul {
        display: none;
    }

    nav .fa-bars {
        display: flex;
        font-size: 1.5rem;
        cursor: pointer;
        color: #fff;
    }

    .main-img {
        height: 70%;
    }
}

@media screen and (max-width: 768px) {
    .main {
        background: #fafafa url(./images/mobile-1.svg) bottom -250px right -100px/contain no-repeat;
    }

    nav .fa-bars {
        color: #000;
    }

    .main-content {
        display: block;
        justify-content: center;
        max-width: 70%;
    }

    .main-col-left {
        margin-top: 0rem;
        display: flex;
        justify-content: center;
        min-width: 100%;
    }

    .email {
        width: 100%;
    }

    .main-btn {
        width: 100%;
    }

    .main-img {
        top: 75%;
    }
}

@media screen and (max-width: 620px) {
    .main-img {
        display: none;
    }
}
